figure {
  margin: 0;
  vertical-align: middle; }

menu {
  padding-left: 0; }

#poststuff h2.hndle {
  font-size: 16px; }

:root .acf-flexible-content .layout .acf-fc-layout-handle {
  color: #131516 !important;
  font-size: 16px; }

:root .acf-flexible-content .layout .acf-fc-layout-order {
  background-color: #005f96;
  color: #fefefe;
  position: relative;
  top: -2px; }

[data-name="highlight"] td.acf-fields {
  display: block; }

[data-name="highlight"] .acf-table > tbody > tr > td {
  border-top-width: 1px; }
  [data-name="highlight"] .acf-table > tbody > tr > td:first-child {
    border-top-width: 0; }

[data-name="highlight"] .acf-table > tbody > tr:first-child > td,
[data-name="highlight"] .acf-table > tbody > tr[data-id="3"] > td {
  border-left-width: 0; }
  [data-name="highlight"] .acf-table > tbody > tr:first-child > td:last-child,
  [data-name="highlight"] .acf-table > tbody > tr[data-id="3"] > td:last-child {
    border-left-width: 0; }

[data-name="highlight"] .acf-table tbody {
  display: flex;
  flex-wrap: wrap; }

[data-name="highlight"] tr.acf-row {
  display: block;
  flex: 1;
  min-width: 33.333%;
  max-width: 50%;
  z-index: initial; }

[data-name="highlight"] td.acf-row-handle {
  display: block;
  width: auto;
  border-width: 0 1px 1px 1px; }

[data-name="highlight"] .acf-repeater .acf-row-handle.remove {
  border-right-width: 0;
  min-height: 16px; }

[data-name="highlight"] .acf-repeater .acf-row-handle .acf-icon.-minus {
  left: 50%;
  margin: -9px 0 0 -8px; }

[data-name="highlight"] .acf-repeater .acf-row-handle .acf-icon.-plus.small {
  right: 0;
  margin: 6px 10px 0 0;
  z-index: 1; }

[data-name="duplo"] .acf-field .acf-button.disabled {
  display: none; }
